.custom-link {
	display: block;
	color: #fff !important;
	position: relative;
	text-decoration: none;
	padding-top: 26px !important;
  padding-bottom: 0 !important;
	padding-right: 10px ;
	padding-left: 10px ;
  font-size: 20px;
	line-height: 20px;
  font-weight: 700;
  }
  
  .custom-link:hover {
	color: #fff !important;
	background: transparent;
	transition: all 0.3s ease;
  }
  
  .custom-link:hover:before {
	position: absolute;
	content: "";
	width: 100%;
	left: 0;
	background: #2793FD;
	transition: all 0.5s ease;
  }
  
  .custom-link:before {
	position: absolute;
	content: "";
	height: 50%;
	background: #2793FD;
	right: 0;
	width: 0%;
	top: 18px;
	z-index: -1;
	transition: all 0.6s ease;
  }
  
  .custom-link:after {
	position: absolute;
	content: "";
	height: 50%;
	background: #2793FD;
	right: 0;
	width: 0%;
	bottom: -8px;
	z-index: -1;
	transition: all 0.4s ease;
  }
  
  .custom-link:hover:after {
	position: absolute;
	content: "";
	width: 100%;
	left: 0;
	background: #2793FD;
	transition: all 0.35s ease;
  }
  
